import { notFound } from 'next/navigation'
import { prisma } from '@/lib/db/prisma'
import { FormatForm } from '@/components/formats/FormatForm'

interface PageProps {
  params: Promise<{ id: string }>
}

export default async function EditFormatPage({ params }: PageProps) {
  const { id } = await params

  const format = await prisma.scriptFormat.findUnique({
    where: { id, deletedAt: null },
  })

  if (!format) {
    notFound()
  }

  const projects = await prisma.project.findMany({
    where: { deletedAt: null },
    select: { id: true, name: true },
    orderBy: { name: 'asc' },
  })

  return (
    <div className="max-w-3xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-neutral-900">Edit Format</h1>
        <p className="mt-2 text-neutral-600">
          Update your script format template
        </p>
      </div>

      <FormatForm projects={projects} initialData={format} />
    </div>
  )
}
